


















































































































































































.el-loading-spinner .path {
	stroke: #66615b !important;
}
.shift-fill {
	&:nth-child(even) {
		background-color: lighten(#285281, 50%) !important;
		color: lighten(#285281, 50%) !important;
	}

	&:nth-child(odd) {
		background: #285281 !important;
		color: #285281 !important;
	}
}
.entity-bordered {
	&:first-child {
		border-top: none;
		border-bottom: none;
	}
	&:last-child {
		border-bottom: none;
	}
	border-top: 1px solid #dee2e6;
	height: 21px;
}
